<template>
    <div class="ex-dialog">
        <jc-header :title="'消息盒子'" :left-options="{showBack:true}" :right-options="{showMore:true}"></jc-header>
        <div class="page-toast-container">
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogAlert">Alert</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogNoTitle">Alert无标题</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogConfirm">确认消息</jc-button>
            </div>
            <!--<div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogOthers">插槽</jc-button>
            </div>-->
            <!--<jc-dialog :value="show" :overlay="true" title="插槽">
              <div>aaa</div>
            </jc-dialog>-->
        </div>
    </div>
</template>

<script>
    export default {
        name: 'ExDialog',
        data() {
            return {
                show: false
            };
        },
        created() {
          
        },
        methods: {
          dialogAlert() {
            this.$dialog.alert({
              title:'Alert',
              message:'内容内容内容内容内容内容内容内容内容内容容内容内容内容内容内容内容内容内容内容'
              
            })
          },
          dialogNoTitle() {
            this.$dialog.alert({
              message:'内容内容内容内容内容内容内容内容内容内容容内容内容内容内容内容内容内容内容内容'
            })
          },
          dialogConfirm() {
            this.$dialog.confirm({
              message:'内容内容内容内容内容内容内容内容内容内容容内容内容内容内容内容内容内容内容内容'
            })
            .then(res=>{
              alert('这是确认的回调');
            })
          },
          dialogOthers() {
            this.show = true
          }
        }
    };
</script>
