<template>
    <div>
        <jc-header :title="'日期选择demo'" :left-options="{showBack:true,backText:''}"></jc-header>
        <div class="page-datetime-wrapper">
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="open('picker1')">时间</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="open('picker2')">日期与时间控件</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="open('picker3')">日期</jc-button>
            </div>
        </div>
        <jc-datetime-picker
                ref="picker1"
                type="time"
                v-model="value1"
                @confirm="handleChange">
        </jc-datetime-picker>
        <jc-datetime-picker
                ref="picker2"
                type="datetime"
                v-model="value2"
                @confirm="handleChange">
        </jc-datetime-picker>
        <jc-datetime-picker
                ref="picker3"
                type="date"
                v-model="value3"
                :start-date="new Date(new Date().getFullYear() - 5, 0, 1)"
                @confirm="handleChange">
        </jc-datetime-picker>
    </div>
</template>

<script>
    export default {
        name: "EXdatetime-picker",
        data(){
            return {
                value1:null,
                value2:null,
                value3:null
            }
        },
        methods:{
            handleChange(value) {
                alert('选择的日期：'+value.toString())
            },
            open(picker) {
                this.$refs[picker].open();
            },
        }
    }
</script>

<style scoped>
    .page-datetime-wrapper {
        padding: 0 20px;
        top: 50%;
        position: absolute;
        width: 100%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
</style>