<template>
    <div class="ex-kalendar">
        <jc-header :title="'标题'" :left-options="{showBack:true}" :right-options="{showMore:true}">
            </jc-header>
        <jc-kalendar @select="select" :animation="true" :point="point"></jc-kalendar>
        <div class="header">
          <div>{{ date | formartDate }}</div>
          <i class="jcm-jiahao1" @click="addCell"></i>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'ExKalendar',
        filters: {
          formartDate(value) {
            if(!value) {
              return ''
            }
            return new Date(value).getFullYear() + '年' + (~~new Date(value).getMonth() + 1) + '月' + new Date(value).getDate() + '日';
          }
        },
        data() {
            return {
                input2: '',
                date: '',
                cells : [],
                point: [[false,false,false,false,false,false,false],[true,true,false,true,true,true,false],
                [true,true,false,true,true,true,false],[true,true,false,true,true,true,false],[false,false,false,false,false,false,false],[false,false,false,false,false,false,false]]
            };
        },
        methods: {
          select(d) {
            this.date = d;
          },
          addCell() {
            this.cells.push();
          }
        }
    };
</script>
<style lang="scss">
.ex-kalendar {
  .header {
    display:flex;
    justify-content: space-between;
    color:#757a95;
    font-size:.37rem;
    padding:.5rem;
    background:#f7faff;
    .jcm-jiahao1 {
      font-size:.5rem;
      color:#6997ed;
      font-weight: bold;
    }
  }
}
</style>