<template>
    <div class="ex-toast">
        <jc-header :title="'标题'" :left-options="{showBack:true}" :right-options="{showMore:true}"></jc-header>
        <div class="page-toast-container">
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastMsg">轻提示</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastPositionTop">位置靠上</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastPositionBottom">位置靠下</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastClick">不可点击</jc-button>
            </div>
            <div style="padding-top: 10px; display:flex; justify-content: space-around;" >
              <jc-button :type="'primary'" @click.native="toastLoading">加载</jc-button>
              <jc-button :type="'primary'" @click.native="toastClose">关闭</jc-button>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'ExToast',
        data() {
            return {
                msg:'提示信息',
                show:false
            };
        },
        created(){
          
        },
        computed:{
          style() {
            return {
              'display':this.show
            }
          }
        },
        methods: {
          toastMsg() {
            this.$toast('轻提示')
          },
          toastClick() {
            this.$toast({
              message:'点击无效',
              forbidClick: true
            })
          },
          toastLoading() {
            this.$toast.loading({
              forbidClick:false
            })
          },
          
          toastClose() {
            this.$toast.clear()
          },
          toastPositionTop() {
            this.$toast({
              position:'top',
              message: '靠上'
            })
          },
          toastPositionBottom() {
            this.$toast({
              position:'bottom',
              message: '靠下'
            })
          }
        }
    };
</script>
<style>
.page-toast-container {
        padding: 0 20px;
        top: 50%;
        position: absolute;
        width: 100%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    .hover {
        width:100vw;
        height:100vh;
        display:flex;
        flex-flow: column;
        justify-content: flex-end;
        background: rgba(0, 0, 0, 0.7);
        z-index: 9999;
    }
</style>