<template>
    <div>
        <div>
            <div class="header-title">组件</div>
        </div>
        <ul class="ex-list">
            <li v-for="item in lists" :key="item.path">
                <jc-cell :title="item.name" is-link :to="item.path"></jc-cell>
            </li>
        </ul>
    </div>
</template>
<script>
import router from '@/components/example/router';
export default {
    name: 'ExList',
    mounted(){
        let _this = this;
        this.$nextTick(()=>{
            if(router && router.options && Array.isArray(router.options.routes)){
                _this.lists = router.options.routes.filter(t => t.znName).map((item)=>{
                    return {
                        path: item.path,
                        name: item.znName
                    }
                });
            }
        });
    },
    data(){
        return {
            lists: []
        }
    }
}
</script>
<style>
li{
    display: block;
}
</style>
