import Vue from 'vue'
import Router from 'vue-router'
import ExList from '@/components/example/list'
import ExContainer from '@/components/example/packages/ex_container'
import ExHeader from '@/components/example/packages/ex_header'
import ExInput from '@/components/example/packages/ex_input'
import ExButton from '@/components/example/packages/ex_button'
import ExTable from '@/components/example/packages/ex_table'
import ExCell from '@/components/example/packages/ex_cell'
import ExNavbar from '@/components/example/packages/ex_navbar'
import ExTabbar from '@/components/example/packages/ex_tabbar'
import ExSelect from '@/components/example/packages/ex_select'
import ExForm from '@/components/example/packages/ex_form'
import ExFlexbox from '@/components/example/packages/ex_flexbox'
import ExGrid from '@/components/example/packages/ex_grid'
import ExCard from '@/components/example/packages/ex_card'                 //卡片
import ExSwitch from '@/components/example/packages/ex_switch'             //开关
import ExSlider from '@/components/example/packages/ex_slider'             //滑块
import ExProgress from '@/components/example/packages/ex_progress'         //进度条
import ExSearch from '@/components/example/packages/ex_search'             //搜索
import ExActionsheet from '@/components/example/packages/ex_actionsheet'   //选择
import ExRadio from '@/components/example/packages/ex_radio'               //单选
import ExChecklist from '@/components/example/packages/ex_checklist'       //多选
// import ExPicker from '@/components/example/packages/ex_picker'          //picker选择
import ExCommented from '@/components/example/packages/ex_commented'       //评论
import ExDatetimePicker from '@/components/example/packages/ex_datetime_picker'       //日期选择
import ExIndexlist from '@/components/example/packages/ex_indexlist'                  //索引列表


//以下为开发子路由，防止本文件提交的冲突
import suixRouter from './routers/suix.js'
import zhengdjRouter from './routers/zhengdj.js'
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'list',
            znName: '',
            component: ExList
        },
        {
            path: '/index-list',
            name: 'index-list',
            znName: '索引列表',
            component: ExIndexlist
        },
        {
            path: '/datetime-picker',
            name: 'datetime-picker',
            znName: '日期选择',
            component: ExDatetimePicker
        },
        {
            path: '/commented',
            name: 'commented',
            znName: '评论',
            component: ExCommented
        },
/*        {
            path: '/picker',
            name: 'picker',
            znName: 'picker',
            component: ExPicker
        },*/
        {
            path: '/checklist',
            name: 'checklist',
            znName: '多选',
            component: ExChecklist
        },
        {
            path: '/actionsheet',
            name: 'actionsheet',
            znName: '选择',
            component: ExActionsheet
        },
        {
            path: '/radio',
            name: 'radio',
            znName: '单选',
            component: ExRadio
        },
        {
            path: '/search',
            name: 'search',
            znName: '搜索',
            component: ExSearch
        },
        {
            path: '/progress',
            name: 'progress',
            znName: '进度条',
            component: ExProgress
        },
        {
            path: '/slider',
            name: 'slider',
            znName: '滑块',
            component: ExSlider
        },
        {
            path: '/card',
            name: 'card',
            znName: '卡片',
            component: ExCard
        },
        {
            path: '/switch',
            name: 'switch',
            znName: '开关',
            component: ExSwitch
        },
        {
            path: '/container',
            name: 'container',
            znName: '容器',
            component: ExContainer
        },
        {
            path: '/header',
            name: 'header',
            znName: '头部导航',
            component: ExHeader
        },
        {
            path: '/input',
            name: 'input',
            znName: '文本输入框',
            component: ExInput
        },
        {
            path: '/button',
            name: 'button',
            znName: '按钮',
            component: ExButton
        },
        {
            path: '/table',
            name: 'table',
            znName: '表格',
            component: ExTable
        },
        {
            path: '/cell',
            name: 'cell',
            znName: '列表',
            component: ExCell
        },
        {
            path: '/navbar',
            name: 'navbar',
            znName: '顶部选项卡',
            component: ExNavbar
        },
        {
            path: '/tabbar',
            name: 'tabbar',
            znName: '底部选项卡',
            component: ExTabbar
        },
        {
            path: '/select',
            name: 'select',
            znName: '页面下拉选',
            component: ExSelect
        },
        {
            path: '/form',
            name: 'form',
            znName: '表单',
            component: ExForm
        },
        {
            path: '/flexbox',
            name: 'flexbox',
            znName: '栅格',
            component: ExFlexbox
        },
        {
            path: '/grid',
            name: 'grid',
            znName: '网格',
            component: ExGrid
        },
        ...suixRouter,
        ...zhengdjRouter
        // 如输入错误结果，重定向到根路径
        // {
        //     path: '*',
        //     redirect: '/'
        // }
    ]
})