import Vue from 'vue';
import App from './Example.vue';
import store from '@/store';
import router from './router';
//嘉诚移动端组件库
import JCUI from '@/jcui/index';
//统一引用组件库的样式
import '@/jcui/themes/studio/studio.scss';
//移动端适配
import '@/assets/flexible.js';
//阻止 vue 在启动时生成生产提示
Vue.config.productionTip = false;
//解决移动端点击事件延时300毫秒问题
import FastClick from 'fastclick';
//调用组件库
Vue.use(JCUI);
//绑定FastClick
FastClick.attach(document.body);

Vue.prototype.$namespace = 'jc';

//初始化vue
new Vue({
    store,
    router,
    render: h => h(App)
}).$mount('#example')