<template>
    <div>
        <jc-header :title="'选择demo'" :left-options="{showBack: true,backText: ''}" :right-options="{showMore:true}"></jc-header>
        <div class="btnbox">
            <jc-button :type="'primary'" :full="true" @click="show1 = true">弹出选择</jc-button>
        </div>
        <jc-actionsheet v-model="show1" :actions="actions" @select="onSelect" :title="title" @cancel="onCancel" :cancelText="'取消'"></jc-actionsheet>
       
    </div>
</template>

<script>
export default {
    name: "ExActionsheet",
    data() {
        return {
            show1: false,
            title: "是否删除信息？",
            actions: [
                { name: '是',className: 'jc-actionsheet__item__red'},
                { name: '否'}
            ]
        };
    },
    methods: {
        onSelect(item) {
            this.show1 = false;
            this.$toast(item.name);
        },
        onCancel() {
            this.$toast("cancel");
        }
    }
};
</script>
<style>
.btnbox {
    padding: 0 20px;
    top: 50%;
    position: absolute;
    width: 100%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}
</style>