<template>
    <div>
        <jc-header :title="'删除功能'" :left-options="{showBack:true,backText:''}" :right-options="{showMore:true}"></jc-header>
        <jc-cell-swipe :right="true" :on-close="onClose" :cancelText="'取消收藏'" :deleteText="'删除'">
            <jc-cell :title="'标题文字'" :value="'icon是图片'" ref="cell"></jc-cell>

        </jc-cell-swipe>
    </div>
</template>
<script>
export default {
    name: "ExCellSwipe",
    methods: {
        onClose(clickPosition, instance) {
            switch (clickPosition) {
                case "left":
                case "cell":
                case "outside":
                    instance.close();
                    break;
                case "rightCancel":
                    this.$dialog
                        .confirm({
                            message: "确定取消收藏吗？"
                        })
                        .then(res => {
                            alert(res)
                            instance.close();
                        });
                    break;
                case "rightDelete":
                    this.$dialog
                        .confirm({
                            message: "确定删除吗？"
                        })
                        .then(res => {
                            alert(res)
                            instance.close();
                        });
                    break;
            }
        }
    }
};
</script>
<style lang="scss">
.jc-cell-swipe {
    &__right {
        .cancel {
            color: #ffffff;
            font-size: 0.48rem;
            padding: 0 0.47rem;
            height: 1.72rem;
            display: inline-block;
            text-align: center;
            line-height: 1.72rem;
            background-color: #ddd;
        }
        .delete {
            color: #ffffff;
            font-size: 0.48rem;
            padding: 0 0.47rem;
            height: 1.72rem;
            display: inline-block;
            text-align: center;
            line-height: 1.72rem;
            background-color: #ff4e4e;
        }
    }
}
</style>